import { asyncRequest, listen } from './utils';

/**
 * Item take actions
 */
listen('.item-take', 'click', function(e) {
  e.preventDefault();
  const id = jQuery(this).data('id');
  const collectionName = jQuery(this).attr('data-collection-name');
  const callback = function(response) {
    const username = JSON.parse(response).actor.username;
    jQuery(`#item-taken-by-${id}`).html(username);
    jQuery(`#item-take-${id}`).hide();
    jQuery(`#item-release-${id}`).show();
  }
  asyncRequest(
    'PATCH',
    `${collectionName}/${id}/take`,
    {},
    callback
  )
});

/**
 * Item release action
 */
listen('.item-release', 'click', function(e) {
  e.preventDefault();
  const id = jQuery(this).data('id');
  const collectionName = jQuery(this).attr('data-collection-name');
  const callback = function(response) {
    jQuery(`#item-taken-by-${id}`).html('');
    jQuery(`#item-release-${id}`).hide();
    jQuery(`#item-take-${id}`).show();
  }
  asyncRequest(
    'PATCH',
    `${collectionName}/${id}/release`,
    {},
    callback
  )
});

/**
 * Item delete action
 */
listen('.item-delete', 'click', function(e) {
  e.preventDefault();
  const id = jQuery(this).data('id');
  const collectionName = jQuery(this).attr('data-collection-name');
  const callback = function(response) {
    jQuery(`#item-row-${id}`).remove();
  }
  jQuery.confirm({
    boxWidth: '30%',
    title: 'Yaqiin?',
    content: 'Yaqqin niih mo dideleteee? Ga nyesel nantinya? Awas looh nyeseel..',
    buttons : {
      confirm: {
        text: 'Yaqiinn, seyaqin yaqinnyaaaa...',
        action: function() {
          asyncRequest(
            'DELETE',
            `${collectionName}/${id}`,
            {},
            callback
          )
        },
        btnClass: 'btn-blue',
      },
      'Ndak kok mas, cuma becanda': function() {
        text: 'Ndak kok mas, cuma becanda..'
      }
    }
  })
});


