import * as NProgress from 'nprogress'
  
/**
 * Create async request
 */
export function listen(identifier, event, callback) {
  jQuery(document).on(event, identifier, callback)
}

/**
 * Create async request
 */
export function asyncRequest(
  method,
  url,
  data = {},
  success = null
) {
  NProgress.start()

  let request = {
    method,
    url,
    data,
    success: function (response) {
      NProgress.done()
      success(response)
    },
    error: function (response) {
      NProgress.done()
      let message = response.responseJSON.meta.message 
      jQuery.gritter.add({
        text: 'Error : ' + message,
        sticky: false,
        time: 3000
      });
    }
  };

  jQuery.ajax(request)
}



